const { Component } = Shopware;
import template from './dreisc-seo-tabs-seo-url.html.twig';
import './dreisc-seo-tabs-seo-url.scss';

Component.extend('dreisc-seo-tabs-seo-url', 'sw-seo-url', {
    template,

    inject: [
        'dreiscSeoCustomConfigApiService'
    ],

    props: {
        area: {
            type: String,
            required: true
        },

        hasParent: {
            type: Boolean,
            required: true
        },

        entity: {
            type: Object,
            required: true
        },

        inheritedEntity: {
            type: Object,
            default() {
                return null;
            }
        }
    },

    data() {
        return {
            customConfig: null,
            canonicalData: {},
            canonicalInheritData: {},

            canonicalLinkTypes: [
                { key: null, name: this.$tc('dreiscSeoDataSeoTab.seoUrl.canonicalLinkTypes.UseSeoPath')},
                { key: 'ExternalUrl', name: this.$tc('dreiscSeoDataSeoTab.seoUrl.canonicalLinkTypes.ExternalUrl')},
                { key: 'ProductUrl', name: this.$tc('dreiscSeoDataSeoTab.seoUrl.canonicalLinkTypes.ProductUrl')},
                { key: 'CategoryUrl', name: this.$tc('dreiscSeoDataSeoTab.seoUrl.canonicalLinkTypes.CategoryUrl')}
            ],

            canonicalLinkVariantTypes: [
                { key: null, name: this.$tc('dreiscSeoDataSeoTab.seoUrl.canonicalLinkTypes.InheritValue')},
                { key: 'UseSeoPath', name: this.$tc('dreiscSeoDataSeoTab.seoUrl.canonicalLinkTypes.UseSeoPath')},
                { key: 'ExternalUrl', name: this.$tc('dreiscSeoDataSeoTab.seoUrl.canonicalLinkTypes.ExternalUrl')},
                { key: 'ProductUrl', name: this.$tc('dreiscSeoDataSeoTab.seoUrl.canonicalLinkTypes.ProductUrl')},
                { key: 'CategoryUrl', name: this.$tc('dreiscSeoDataSeoTab.seoUrl.canonicalLinkTypes.CategoryUrl')}
            ]
        }
    },

    computed: {
        defaultSalesChannelId() {
            if(null === this.customConfig) {
                return null;
            }

            if (this.customConfig.hasOwnProperty('seoUrl') && this.customConfig.seoUrl.hasOwnProperty('defaultSalesChannelId')) {
                /** Check for valid id hash */
                if (32 === this.customConfig.seoUrl.defaultSalesChannelId.length) {
                    return this.customConfig.seoUrl.defaultSalesChannelId;
                }
            }

            return null;
        },

        customFields() {
            if (null === this.entity) {
                return {};
            }

            if (!this.entity.customFields) {
                this.entity.customFields = {};
            }

            if(0 === Object.keys(this.entity.customFields)) {
                return {};
            }

            return this.entity.customFields;
        },

        customFieldsInherit() {
            if (null === this.inheritedEntity) {
                return {};
            }

            if (!this.inheritedEntity.customFields) {
                this.inheritedEntity.customFields = {};
            }

            if(0 === Object.keys(this.inheritedEntity.customFields)) {
                return {};
            }

            return this.inheritedEntity.customFields;
        },

        considerInheritanceContext() {
            let context = Shopware.Context.api;
            context.inheritance = true;

            return context;
        }
    },

    created() {
        this.createComponent();
    },

    methods: {
        createComponent() {
            this.dreiscSeoCustomConfigApiService.getCustomConfig().then(customConfigPromise => {
                this.customConfig = customConfigPromise.defaultCustomSettings;
            });
        },

        onSalesChannelChanged(salesChannelId) {
            this.currentSalesChannelId = salesChannelId;
            this.refreshCurrentSeoUrl();
        },

        loadCanonicalData() {

            if ('object' === typeof this.customFields.dreisc_seo_canonical_link_type) {
                /** Deprecated way */
                this.canonicalData = {
                    dreisc_seo_canonical_link_type: this.customFields.dreisc_seo_canonical_link_type[this.currentSalesChannelId],
                    dreisc_seo_canonical_link_reference: this.customFields.dreisc_seo_canonical_link_reference[this.currentSalesChannelId]
                };

                this.canonicalInheritData = {
                    dreisc_seo_canonical_link_type: null,
                    dreisc_seo_canonical_link_reference: null
                };
            } else {
                /** New way */
                this.canonicalData = {
                    dreisc_seo_canonical_link_type: this.getSalesChannelItem(this.customFields.dreisc_seo_canonical_link_type, this.currentSalesChannelId, 'type'),
                    dreisc_seo_canonical_link_reference: this.getSalesChannelItem(this.customFields.dreisc_seo_canonical_link_reference, this.currentSalesChannelId, 'reference')
                };

                this.canonicalInheritData = {
                    dreisc_seo_canonical_link_type: this.getSalesChannelItem(this.customFieldsInherit.dreisc_seo_canonical_link_type, this.currentSalesChannelId, 'type'),
                    dreisc_seo_canonical_link_reference: this.getSalesChannelItem(this.customFieldsInherit.dreisc_seo_canonical_link_reference, this.currentSalesChannelId, 'reference')
                };
            }
        },

        getSalesChannelItem(array, currentSalesChannelId, itemKey) {
            if (!array) {
                return null;
            }

            const arrayClone = JSON.parse(array);

            if (0 === arrayClone.length) {
                return null;
            }

            let returnValue = null;
            arrayClone.forEach((arrayItem) => {
                if (arrayItem.salesChannelId && arrayItem.salesChannelId === currentSalesChannelId && arrayItem[itemKey]) {
                    returnValue = arrayItem[itemKey];
                }
            });

            return returnValue;
        },

        buildReturn(array, field, value) {
            let updatedArray = [];

            if ('string' === typeof array) {
                const decodedArray = JSON.parse(array);

                decodedArray.forEach((arrayItem) => {
                    if (arrayItem.salesChannelId && arrayItem.salesChannelId !== this.currentSalesChannelId) {
                        updatedArray.push(arrayItem);
                    }
                });
            }

            let newItem = { salesChannelId: this.currentSalesChannelId };
            newItem[field] = value;
            updatedArray.push(newItem)

            return JSON.stringify(updatedArray);
        },

        getBreadcrumb(item) {
            let breadcrumb = [];

            if('object' !== typeof item || 0 === item.breadcrumb.length) {
                return '';
            }

            item.breadcrumb.forEach((item) => {
                breadcrumb.push(item);
            });

            /** Remove the last element */
            breadcrumb.pop();

            return breadcrumb.join('  ');
        }
    },

    watch: {
        customFields: {
            deep: true,
            handler() {
                this.loadCanonicalData();
            }
        },

        customFieldsInherit: {
            deep: true,
            handler() {
                this.loadCanonicalData();
            }
        },

        'currentSalesChannelId'() {
            this.loadCanonicalData();
        },

        canonicalData: {
            deep: true,
            handler() {
                this.customFields.dreisc_seo_canonical_link_type = this.buildReturn(
                    this.customFields.dreisc_seo_canonical_link_type,
                    'type',
                    this.canonicalData.dreisc_seo_canonical_link_type
                );

                this.customFields.dreisc_seo_canonical_link_reference = this.buildReturn(
                    this.customFields.dreisc_seo_canonical_link_reference,
                    'reference',
                    this.canonicalData.dreisc_seo_canonical_link_reference
                );

                this.customFieldsInherit.dreisc_seo_canonical_link_type = this.buildReturn(
                    this.customFieldsInherit.dreisc_seo_canonical_link_type,
                    'type',
                    this.canonicalInheritData.dreisc_seo_canonical_link_type
                );

                this.customFieldsInherit.dreisc_seo_canonical_link_reference = this.buildReturn(
                    this.customFieldsInherit.dreisc_seo_canonical_link_reference,
                    'reference',
                    this.canonicalInheritData.dreisc_seo_canonical_link_reference
                );


                // this.customFields.dreisc_seo_canonical_link_type = JSON.stringify([{ salesChannelId: this.currentSalesChannelId, type: this.canonicalData.dreisc_seo_canonical_link_type }]);
                // this.customFields.dreisc_seo_canonical_link_reference = JSON.stringify([{ salesChannelId: this.currentSalesChannelId, reference: this.canonicalData.dreisc_seo_canonical_link_reference }]);
                //
                // this.customFieldsInherit.dreisc_seo_canonical_link_type = JSON.stringify([{ salesChannelId: this.currentSalesChannelId, type: this.canonicalInheritData.dreisc_seo_canonical_link_type }]);
                // this.customFieldsInherit.dreisc_seo_canonical_link_reference = JSON.stringify([{ salesChannelId: this.currentSalesChannelId, reference: this.canonicalInheritData.dreisc_seo_canonical_link_reference }]);;
            }
        }
    }
});
